<template>
    <div class="wuzi">
        <!-- 顶部logo -->
        <img width="100%" src="../../assets/images/wuzi.png" alt="" />
        <!-- <h2>防疫物资</h2> -->
         <div class="wrapper">
            <!-- 选项卡：1. 自己写  2. vant库里面  3. 修改vant样式 /deep/ 类名{ }-->
            <!-- vant库-tabs标签 -->
            <van-tabs v-model="active" animated>
              <van-tab v-for="item in arr" :title="item.title" :key='item.title'background='red'>
                  <!-- <img width="100%" src="../../assets/images/jiujing.png" alt="" /> -->
                  <p><span class="left"></span>{{item.text}}<span class="right"></span></p>
                  <div class="img"><img width="90%" :src="item.img" alt="" /></div>
              </van-tab>
            </van-tabs>      
        </div>

    </div>
</template>

<script>
//导入图片
import img1 from '../../assets/images/jiujing.png'
import img2 from '../../assets/images/kouzhao.png'
// 引入单个组件样式
import 'vant/lib/tab/style/less.js';

export default {
    data(){
        return{
            active: 0,
            arr:[
                {
                    img:img1,
                    title:'消毒剂',
                    text:'消毒剂指南'
                },
                {
                    img:img2,
                    title:'口罩',
                    text:'口罩指南'
                }
            ]

        }
    }
}
</script>

<style lang="less" scoped>
    
.wuzi{
    background-color: rgb(51,67,255)
}
.wuzi .wrapper{
    top:2.6rem;
    margin-left: 0.3rem;
    margin-right: 0.3rem;
    
}

/deep/ .van-tab__pane-wrapper{
    background-color: white;
    border-radius: 0.1rem;
    p{
        margin-top: 0.4rem;
        margin-bottom: 0.4rem;
        font-size: 0.4rem;
        text-align: center;
        // background-color: red;
        .left{
            margin-right:0.2rem;
            width: 0.2rem;
            height: 0.03rem;
            display: inline-block;
            background-color: rgba(70, 53, 53, 0.582);
            vertical-align: middle;
        }
        .right{        
            margin-left:0.2rem;
            width: 0.2rem;
            height: 0.03rem;
            display: inline-block;
            background-color: rgba(70, 53, 53, 0.582);
            vertical-align: middle;
        
        }
    }
    .img{
        text-align: center;
    }
    // img{
    //     border: 1px solid red;
    //     // margin:0 auto;
        
    // }
}
/deep/ .van-tab__text--ellipsis{
    background-color:rgb(104,116,255);
    padding: 0.1rem;
    color: white;
    padding: 0.08rem 0.4rem 0.08rem 0.4rem;
    font-size: 0.26rem;
}
/deep/ .van-tabs__nav{
    background-color: rgb(51,67,255);
}
/deep/ .van-tab--active .van-tab__text--ellipsis{
    background-color: rgb(247,130,7);
}
/deep/ .van-tabs__line{
    background-color: rgb(51,67,255);
}
</style>


